<template>
	<div class="practice-main" v-if="myIsPractice">
		<div class="practice-dialog">
				<div class="practice-box">
					<div class="practice-number">
						<div class="practice-item" v-for="(item,index) in practiceList">
							<span :class="{'on': item.on, 'success': item.SendQuestion > 0, 'error': item.SendAnswer > 0}" @click="practiceTitleSelect(index)">{{item.Id}}</span>
						</div>
					</div>
					<div class="practice-btn">
						<a href="javascript:;" @click="sendTitle('1')"></a>
						<a href="javascript:;" @click="sendTitle('2')"></a>
					</div>
				</div>
				<div class="practice-close">
					<i @click="closePracticeTitle" class="iconfont icon-20150825tubiaolianxizhuanhuan04"></i>
				</div>
			</div>
			<!--  -->
			<v-mask :flag="myIsPractice"></v-mask>
	</div>
</template>

<script>

// 
import util from '@/assets/js/util'

// 
import mask from '../functionComponents/mask'

// 
import { Toast } from 'mint-ui';

export default {
  name: 'practicemain',
	props: ['isPractice'],
  data () {
    return {
			// 1. 
			myIsPractice: this.isPractice,
			//  
			practiceList: [],
			// 
			parcticeIndex: false
    }
  },
	watch: {
		isPractice(val) {
			// 2. isPracticewatchmyIsPractice
			this.myIsPractice = val;
			if (this.myIsPractice) {
				console.log('');
				this._ajax(`${this.$store.state.url}/wx/powderSystem/getDayPractiseQuestionList`, (res) => {
					// SendAnswer 
					// SendQuestion  
					this.practiceList = res.body;
					console.log(this.practiceList);
					for (let i in this.practiceList) {
						this.$set(this.practiceList[i],'on',false);
					}
				})
			}
		},
		myIsPractice(val) {
			 this.$emit("on-parctice-change",val);// 3.myIsPractice
		}
	},
  methods: {
  	// 
    practiceTitleSelect(index) {
			this.parcticeIndex = index;
      for (let i in this.practiceList) {
        if (i == index) {
						this.practiceList[i].on = true;
        } else {
 						this.practiceList[i].on = false;
        }
      }
    },
  	// 
    closePracticeTitle() {
      this.myIsPractice = !this.myIsPractice;
			this.parcticeIndex = false;
    },
		// 
		// type1    
		// type2    
		sendTitle(type) {
			console.log('index = ' + this.parcticeIndex);
			 if (this.parcticeIndex === false) {
					Toast('');
					return false;
			 } else if (this.practiceList[this.parcticeIndex].on) {
				 	let qid = this.practiceList[this.parcticeIndex].QuestionsId;
					this._ajax(`${this.$store.state.url}/wx/powderSystem/getDayPractiseQuestionById?groupId=${userObj.groupId}&qid=${qid}&sendType=${type}&adminId=${userObj.adminId}`, (res) => {
						// 
						// Question.Title     
						// Question.options   
						// Question.options.Option  
						// Question.options.IsTrue  0   1  

						// 
						// Question.Explain  
						// Question.People   
						// Question.Accuracy 
						let list = this.$store.state.practiceObj.selfSendPracticeList;
						let time = new Date();
						list.push(res.body.Question);
						let listLength = list.length - 1;
						// id
						this.$set(list[listLength], 'PractiseItem', this.practiceList[this.parcticeIndex].PractiseItem);
						// 
						this.$set(list[listLength], 'ismy', true);
						// uid
						this.$set(list[listLength], 'Uid', userObj.uid);
						// 
						this.$set(list[listLength], 'dataType', 2);
						// 
						this.$set(list[listLength], 'Timer', time.getTime());
						
						if (type == 1) {
							// 
							this.$set(list[listLength], 'submitStatus', false);
						} else if (type == 2) {
							// 
							this.$set(list[listLength], 'submitStatus', true);
							this.$set(list[listLength], 'showSuccessAnswer', true);
						}
						// websocket
						MessageSend(JSON.stringify(list[listLength]));
						this.$nextTick(() => {
							util.partScrollToBottom();
						});
						this.closePracticeTitle();
					});
			 }
		},
		// ajax 
    _ajax(url, cb) {
      this.$http.get(url).then( res => {
        if (res.status === 200){
            cb && cb(res);
        }
      }, error => {
       console.log(error);
     });
		}
  },
	components: {
		'v-mask': mask
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../../assets/css/var.scss";
/*  */
.practice-dialog{
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 99;
  width: 300px;
  transform: translate(-50%, -50%);
  .practice-box{
    padding: 15px;
    border-radius: 8px;
    background: #fff;
    .practice-number{
      display: flex;
      flex-wrap: wrap;
      .practice-item{
        display: block;
        margin-bottom: 10px;
        flex: 0 0 20%;
        min-width: 0;
        text-align: center;
        span{
          display: inline-block;
          width: 40px;
          height: 40px;
          line-height: 40px;
          border: 1px solid $bordercolor;
          border-radius: 50%;
          color: $txtcolor;
          &.success{
            background: #83ce83;
            color: #fff;
          }
          &.error{
            background: $errorcolor;
            color: #fff;
          }
					&.on{
            background: #969696;
            color: #fff;
          }
        }
      }
    }
    .practice-btn{
      margin-top: 30px;
      display: flex;
      a{
        display: block;
        padding: 10px;
        flex: 1;
        background: $bluecolor;
        box-sizing: border-box;
        border-radius: 20px;
        text-align: center;
        color: #fff;
        &:nth-child(1) {
          margin-right: 15px;
        }
      }
    }
  }
  .practice-close{
    margin-top: 20px;
    text-align: center;
    .iconfont{
      font-size: 40px;
      color: #fff;
    }
  }
}
</style>



