import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		// vuex 1    2  
		uid: 2,
		// 
		practiceObj:{
			// 
			selfSendPracticeList:[
				// {
				// 	userid: 179, 
				// 	username: "", 
				// 	useravator: "./face.png", 
				// 	type: 1, 
				// 	content: "", 
				// 	contentId: 941,
				// 	timer: 1495790331046, 
				// 	ismy: true, 
				// 	dataType: 1
				// }
			],
			// 
			signIn: false,
			// 
			groupBanned: false,
			// 
			selfBanned: false
		},
		// 
		url: 'http://120.24.6.91:8082'
	},
	mutations: {
		setUid (state, val) {
			state.uid = val;
		},
		setGroupBanned (state, val) {
			state.practiceObj.groupBanned = val;
		},
		setSelfBanned (state, val) {
			state.practiceObj.selfBanned = val;
		}
	}
}) 




